@import '/src/common/scss/common.scss';
@import '/src/components/logo/logo.scss';
@import '/src/components/header/header.scss';

.main-page {
  min-height: 100vh;
  padding: 170px 0 68px;

  background-color: rgba(0, 0, 1, 0.91);
  background-image: url('/static/images/ancient-castle-mountains-generative-ai.png');
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
}
.main-page__container {
  display: flex;
  // align-items: center;
  justify-content: center;
  gap: 127px;
}
.main-page__box {
  display: flex;
  flex-direction: column;
}

.main-page__title {
  width: 580px;
  height: 75px;

  color: #fff;
  // font-family: "Jomhuria";
  font-size: 64px;
  font-style: normal;
  font-weight: 400;
  // line-height: 178.4%;
  padding: 0 0 9px;
 
}
.main-page__subtitle {
  color: #fff;
  // font-family: Inknut Antiqua;
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 125.4%;
}

.main-page__box-inner {
  display: flex;
  flex-direction: column;
  gap: 46px;

  width: 502px;
  height: 247px;
  padding: 24px 28px;
  border-radius: 20px;
  background: rgba(17, 16, 16, 0.71);
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.auth-form__input {
  display: flex;
  align-items: center;
  width: 446px;
  height: 44px;
  padding: 0 30px;

  color: #d9d9d9;
  // font-family: Inknut Antiqua;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  border-radius: 8px;
  border: 1px solid #d9d9d9;
  background: #0e0c0c;

  &:focus {background:  #4D4A4A; outline: none;}
}

.auth-form__btn-list {
  display: flex;
  gap: 18px;
}

.auth-form__btn-item {
  flex: 0 0 calc((100% - 18px) / 2);
  width: calc((100% - 18px) / 2);
}

.auth-form__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 44px;

  color: #000001;
  // font-family: Inknut Antiqua;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;

  border-radius: 8px;
  border: 1px solid #d9d9d9;
  background: #fff;
  

  &:hover, &:focus {background:  #4D4A4A; border-color: #4D4A4A;}
}
.auth-form__btn--transparent {
  color: #fff;
  background-color: transparent;
  border-color: #D9D9D9;
}

.benefits__icon {
  fill: #fff;

  // &:hover, &:focus {fill:  #4D4A4A;}
}

.benefits__list {
  display: flex;
  justify-content: center;
  gap: 54px;
  margin-top: 228px;
}

.benefits__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.benefits__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;

  color: #fff;
  font-family: Inknut Antiqua;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 134.4%;
}

.benefits__text span {
  font-size: 22px;
  font-weight: 600;
}